import {Component, OnInit} from '@angular/core';
import {NavigationEnd, Router} from '@angular/router';

@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.scss']
})
export class EventComponent implements OnInit {
  eventType = '';
  eventId = '';
  eventTitle = 'Đàn cúng Đại đàn chánh lễ - Đại lễ vía Đức Chí Tôn - Tý thời - ngày 09/01/Canh Tý - Thánh Thất Đà Lạt';
  eventLeader = 'Phối sư Thượng Lễ Thanh';

  constructor(private router: Router) {
    router.events.subscribe(e => {
      if (e instanceof NavigationEnd) {
        let url = e.urlAfterRedirects;
        url = url.replace('/', '');
        const params = url.split('?');
        if (params.length > 0) {
          if (params[0] === 'event') {
            const param1s = params[1].split('&');
            this.eventType = param1s[0].replace('type=', '');
            this.eventId = param1s[1].replace('id=', '');
          }
        }
      }
    });
  }

  ngOnInit(): void {
    console.log(this.eventId);
  }


}
