import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {
  isShowDownDownProfile = false;
  isShowDownDownFeature = false;
  constructor() { }

  ngOnInit(): void {
  }
  onShowDropdown(event) {
    if (event.target.id === 'show-profile-btn') {
      this.isShowDownDownProfile = true;
    } else {
      this.isShowDownDownProfile = false;
    }
    if (event.target.id === 'show-features-btn' || event.target.id === 'show-features-btn-link' || event.target.id === 'show-features-btn-icon' || event.target.id === 'show-features-text') {
      this.isShowDownDownFeature = true;
    } else {
      this.isShowDownDownFeature = false;
    }
  }
}
