import {Component, HostListener, OnInit} from '@angular/core';

@Component({
  selector: 'app-add-event',
  templateUrl: './add-event.component.html',
  styleUrls: ['./add-event.component.scss']
})
export class AddEventComponent implements OnInit {
  isShowDropSearchName = false;
  isShowDropSearchMankindType = false;
  isShowDropSearchMankindTypeName = false;
  isShowDropSearchTime = false;
  isShowDropSearchLocation = false;
  isValidEventType = false;
  isReadyEventType = false;
  isValidEventName = true;
  isReadyEventName = false;
  isValidEventTime = true;
  isReadyEventTime = false;
  isValidEventLocation = true;
  isReadyEventLocation = false;
  eventType = '';
  eventName = '';
  eventTime = '';
  eventLocation = '';
  isGodType = false;
  isMankindType = false;
  isValidEventMankindType = false;
  eventMankindType = '';
  eventMankindTypeName = '';
  isReadyEventMankindType = false;
  isReadyEventMankindTypeName = false;
  isShowDropSearchEventLeader = false;
  isValidEventLeader = true;
  eventLeader = '';
  isReadyEventLeader = false;
  isDoneForm = false;
  isShowDropSearchOwnerRole = false;
  isValidEventOwnerRole = false;
  eventOwnerRole = '';
  isReadyEventOwnerRole = false;
  isShowDropSearchOwnerName = false;
  eventOwnerName = '';
  eventOwnerAge = '';
  isReadyEventOwnerName = false;
  isShowBtnToTop = false;
  isReadyEventOwnerAge = false;
  isShowDropSearchOwnerAge = false;
  isValidEventOwnerLocation = false;
  isShowDropSearchOwnerLocationNation = false;
  isReadyEventOwnerLocationNation = false;
  eventOwnerLocationNation = '';
  isForDownLV = true;
  isHasColor = false;
  eventOwnerRoleColor = '';
  isShowDropSearchOwnerRoleColor = false;
  isReadyEventOwnerRoleColor = false;
  eventOwnerHolyName = '';
  eventOwnerLocationProvincial = '';
  isShowDropSearchOwnerLocationProvincial = false;
  isReadyEventOwnerLocationProvincial = false;
  eventOwnerLocationCity = '';
  isShowDropSearchOwnerLocationCity = false;
  isReadyEventOwnerLocationCity = false;

  @HostListener('window:scroll', ['$event'])
  onWindowScroll($event) {
    if (window.scrollY >= 50){
      this.isShowBtnToTop = true;
    }
    else {
      this.isShowBtnToTop = false;
    }
  }
  constructor() {
  }

  ngOnInit(): void {
    this.scrollTo('add-event__item--event-type');
  }
  onShowDropSearchName(event) {
    if (event.target.id === 'form-ctrl-event-name') {
      this.isShowDropSearchName = true;
    } else {
      this.isShowDropSearchName = false;
    }
  }

  onShowDropSearchMankindType(event) {
    if (event.target.id === 'form-ctrl-event-mankind-type') {
      this.isShowDropSearchMankindType = true;
    } else {
      this.isShowDropSearchMankindType = false;
    }
    if (event.target.id === 'form-ctrl-event-mankind-type-name') {
      this.isShowDropSearchMankindTypeName = true;
    } else {
      this.isShowDropSearchMankindTypeName = false;
    }
  }

  onShowDropSearchTime(event) {
    if (event.target.id === 'form-ctrl-event-time') {
      this.isShowDropSearchTime = true;
    } else {
      this.isShowDropSearchTime = false;
    }
  }

  onShowDropSearchLocation(event) {
    if (event.target.id === 'form-ctrl-event-location') {
      this.isShowDropSearchLocation = true;
    } else {
      this.isShowDropSearchLocation = false;
    }
  }

  onShowDropSearchEventLeader(event) {
    if (event.target.id === 'form-ctrl-event-leader') {
      this.isShowDropSearchEventLeader = true;
    } else {
      this.isShowDropSearchEventLeader = false;
    }
  }

  onShowDropSearchOwner(event) {
    if (event.target.id === 'form-ctrl-event-owner-role') {
      this.isShowDropSearchOwnerRole = true;
    } else {
      this.isShowDropSearchOwnerRole = false;
    }
    if (event.target.id === 'form-ctrl-event-owner-role-color') {
      this.isShowDropSearchOwnerRoleColor = true;
    } else {
      this.isShowDropSearchOwnerRoleColor = false;
    }
    if (event.target.id === 'form-ctrl-event-owner-name') {
      this.isShowDropSearchOwnerName = true;
    } else {
      this.isShowDropSearchOwnerName = false;
    }
    if (event.target.id === 'form-ctrl-event-owner-age') {
      this.isShowDropSearchOwnerAge = true;
    } else {
      this.isShowDropSearchOwnerAge = false;
    }
  }
  onShowDropSearchOwnerLocation(event) {
    if (event.target.id === 'form-ctrl-event-owner-location-nation') {
      this.isShowDropSearchOwnerLocationNation = true;
    } else {
      this.isShowDropSearchOwnerLocationNation = false;
    }
    if (event.target.id === 'form-ctrl-event-owner-location-provincial') {
      this.isShowDropSearchOwnerLocationProvincial = true;
    } else {
      this.isShowDropSearchOwnerLocationProvincial = false;
    }
    if (event.target.id === 'form-ctrl-event-owner-location-city') {
      this.isShowDropSearchOwnerLocationCity = true;
    } else {
      this.isShowDropSearchOwnerLocationCity = false;
    }
  }

  onSearchNameChange(searchValue: string): void {
    this.eventName = searchValue;
    if (this.eventName !== '') {
      this.isReadyEventName = true;
    } else {
      this.isReadyEventName = false;
    }
  }

  onSearchMankindTypeChange(searchValue: string): void {
    this.eventMankindType = searchValue;
    if (this.eventMankindType !== '') {
      this.isReadyEventMankindType = true;
    } else {
      this.isReadyEventMankindType = false;
    }
  }

  onSearchMankindTypeNameChange(searchValue: string): void {
    this.eventMankindTypeName = searchValue;
    if (this.eventMankindTypeName !== '') {
      this.isReadyEventMankindTypeName = true;
    } else {
      this.isReadyEventMankindTypeName = false;
    }
  }

  onSearchTimeChange(searchValue: string): void {
    this.eventTime = searchValue;
    if (this.eventTime !== '') {
      this.isReadyEventTime = true;
    } else {
      this.isReadyEventTime = false;
    }
  }

  onSearchEventLeader(searchValue: string): void {
    this.eventLeader = searchValue;
    if (this.eventLeader !== '') {
      this.isReadyEventLeader = true;
    } else {
      this.isReadyEventLeader = false;
    }
  }

  onSearchOwnerRoleChange(searchValue: string): void {
    this.eventOwnerRole = searchValue;
    if (this.eventOwnerRole !== '') {
      this.isReadyEventOwnerRole = true;
    } else {
      this.isReadyEventOwnerRole = false;
    }
  }
  onSearchOwnerRoleColorChange(searchValue: string): void {
    this.eventOwnerRoleColor = searchValue;
    if (this.eventOwnerRoleColor !== '') {
      this.isReadyEventOwnerRoleColor = true;
    } else {
      this.isReadyEventOwnerRoleColor = false;
    }
  }
  onSearchOwnerAgeChange(searchValue: string): void {
    this.eventOwnerAge = searchValue;
    if (this.eventOwnerAge !== '') {
      this.isReadyEventOwnerAge = true;
    } else {
      this.isReadyEventOwnerAge = false;
    }
  }
  onSearchOwnerNameChange(searchValue: string): void {
    this.eventOwnerName = searchValue;
    if (this.eventOwnerName !== '') {
      this.isReadyEventOwnerName = true;
    } else {
      this.isReadyEventOwnerName = false;
    }
    if (this.isHasColor === true){
      this.eventOwnerName.split(/[, ]+/).pop();
      this.eventOwnerHolyName = this.eventOwnerRoleColor + ' ' + this.eventOwnerName.split(/[, ]+/).pop() + ' Thanh';
    }
  }

  onChooseSearchNameValue(selectedSearchValue: string): void {
    this.isShowDropSearchName = false;
    this.eventName = selectedSearchValue;
    if (this.eventName !== '') {
      this.isReadyEventName = true;
    } else {
      this.isReadyEventName = false;
    }
  }

  onChooseSearchOwnerRoleValue(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerRole = false;
    this.eventOwnerRole = selectedSearchValue;
    if (this.eventOwnerRole !== '') {
      this.isReadyEventOwnerRole = true;
      if (this.eventOwnerRole === 'Lễ Sanh' || this.eventOwnerRole === 'Giáo sư'){
        this.isHasColor = true;
      }else {
        this.isHasColor = false;
      }
    } else {
      this.isReadyEventOwnerRole = false;
    }
  }
  onChooseSearchOwnerRoleColorValue(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerRoleColor = false;
    this.eventOwnerRoleColor = selectedSearchValue;
    if (this.eventOwnerRoleColor !== '') {
      this.isReadyEventOwnerRoleColor = true;
    } else {
      this.isReadyEventOwnerRoleColor = false;
    }
  }
  onChooseSearchOwnerNameValue(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerName = false;
    this.eventOwnerName = selectedSearchValue;
    if (this.eventOwnerName !== '') {
      this.isReadyEventOwnerName = true;
    } else {
      this.isReadyEventOwnerName = false;
    }
    if (this.isHasColor === true){
      this.eventOwnerName.split(/[, ]+/).pop();
      this.eventOwnerHolyName = this.eventOwnerRoleColor + ' ' + this.eventOwnerName.split(/[, ]+/).pop() + ' Thanh';
    }
  }
  onSearchOwnerLocationNationChange(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerLocationNation = false;
    this.eventOwnerLocationNation = selectedSearchValue;
    if (this.eventOwnerLocationNation !== '') {
      this.isReadyEventOwnerLocationNation = true;
    } else {
      this.isReadyEventOwnerLocationNation = false;
    }
  }
  onSearchOwnerLocationProvincialChange(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerLocationProvincial = false;
    this.eventOwnerLocationProvincial = selectedSearchValue;
    if (this.eventOwnerLocationProvincial !== '') {
      this.isReadyEventOwnerLocationProvincial = true;
    } else {
      this.isReadyEventOwnerLocationProvincial = false;
    }
  }
  onSearchOwnerLocationCityChange(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerLocationCity = false;
    this.eventOwnerLocationCity = selectedSearchValue;
    if (this.eventOwnerLocationCity !== '') {
      this.isReadyEventOwnerLocationCity = true;
    } else {
      this.isReadyEventOwnerLocationCity = false;
    }
  }

  onChooseSearchMankindTypeValue(selectedSearchValue: string): void {
    this.isShowDropSearchMankindType = false;
    this.eventMankindType = selectedSearchValue;
    if (this.eventMankindType !== '') {
      this.isReadyEventMankindType = true;
    } else {
      this.isReadyEventMankindType = false;
    }
    if (this.eventMankindType === 'Cầu phước'){
      this.isForDownLV = false;
      this.eventMankindTypeName = '';
    }
    else {
      this.isForDownLV = true;
    }
  }

  onChooseSearchMankindTypeNameValue(selectedSearchValue: string): void {
    this.isShowDropSearchMankindTypeName = false;
    this.eventMankindTypeName = selectedSearchValue;
    if (this.eventMankindTypeName !== '') {
      this.isReadyEventMankindTypeName = true;
    } else {
      this.isReadyEventMankindTypeName = false;
    }
  }

  onChooseSearchTimeValue(selectedSearchValue: string): void {
    this.isShowDropSearchTime = false;
    this.eventTime = selectedSearchValue;
    if (this.eventTime !== '') {
      this.isReadyEventTime = true;
    } else {
      this.isReadyEventTime = false;
    }
    console.log(this.isReadyEventTime);
  }

  onChooseSearchOwnerAgeValue(selectedSearchValue: string): void {
    this.isShowDropSearchOwnerAge = false;
    this.eventOwnerAge = selectedSearchValue;
    if (this.eventOwnerAge !== '') {
      this.isReadyEventOwnerAge = true;
    } else {
      this.isReadyEventOwnerAge = false;
    }
    console.log(this.isReadyEventOwnerAge);
  }

  onChooseSearchLocationValue(selectedSearchValue: string): void {
    this.isShowDropSearchLocation = false;
    this.eventLocation = selectedSearchValue;
    if (this.eventLocation !== '') {
      this.isReadyEventLocation = true;
    } else {
      this.isReadyEventLocation = false;
    }
  }

  onChooseSearchEventLeaderValue(selectedSearchValue: string): void {
    this.isShowDropSearchEventLeader = false;
    this.eventLeader = selectedSearchValue;
    if (this.eventLeader !== '') {
      this.isReadyEventLeader = true;
    } else {
      this.isReadyEventLeader = false;
    }
  }

  onSearchLocationChange(selectedSearchValue: string): void {
    this.isShowDropSearchLocation = false;
    this.eventLocation = selectedSearchValue;
    if (this.eventLocation !== '') {
      this.isReadyEventLocation = true;
    } else {
      this.isReadyEventLocation = false;
    }
  }

  onChooseEventType(checkedValue: string): void {
    this.eventType = checkedValue;
    this.isReadyEventType = true;
    if (checkedValue === 'Thiên Đạo') {
      this.isGodType = true;
      this.isMankindType = false;
      this.isValidEventName = true;
    }
    if (checkedValue === 'Nhơn Đạo') {
      this.isMankindType = true;
      this.isGodType = false;
      this.isValidEventMankindType = true;
    }
  }

  scrollTo(eventItem) {
    window.scrollTo({
      top: document.getElementById(eventItem).offsetTop,
    });
    if (eventItem === 'add-event__item--event-type'){
      this.isValidEventType = false;
      this.isDoneForm = false;
      this.scrollTo('add-event__item--event-type');
    }
  }

  showNextField(event) {
    switch (event.target.id) {
      case 'next-from--event-type':
        this.isValidEventType = true;
        this.isValidEventTime = false;
        this.scrollTo('add-event__item--event-time');
        break;
      case 'next-from--event-time':
        this.isValidEventTime = true;
        this.isValidEventLocation = false;
        this.scrollTo('add-event__item--event-location');
        break;
      case 'prev-from--event-time':
        this.isValidEventType = false;
        this.isValidEventTime = true;
        this.eventTime = '';
        this.isReadyEventTime = false;
        this.scrollTo('add-event__item--event-type');
        break;
      case 'prev-from--event-location':
        this.isValidEventTime = false;
        this.isValidEventLocation = true;
        this.eventLocation = '';
        this.isReadyEventLocation = false;
        this.scrollTo('add-event__item--event-time');
        break;
      case 'btn-done':
        this.isDoneForm = true;
        this.isValidEventLeader = true;
        break;
      case 'btn-edit':
        this.isValidEventType = false;
        this.isDoneForm = false;
        this.scrollTo('add-event__item--event-type');
        break;
    }
    if (this.isGodType) {
      switch (event.target.id) {
        case 'next-from--event-location':
          this.isValidEventLocation = true;
          this.isValidEventName = false;
          this.scrollTo('add-event__item--event-name');
          break;
        case 'prev-from--event-name':
          this.isValidEventName = true;
          this.isValidEventLocation = false;
          this.eventName = '';
          this.isReadyEventName = false;
          this.scrollTo('add-event__item--event-location');
          break;
        case 'next-from--event-name':
          this.isValidEventName = true;
          this.isValidEventLeader = false;
          this.scrollTo('add-event__item--event-leader');
          break;
      }
    }
    if (this.isMankindType) {
      switch (event.target.id) {
        case 'next-from--event-location':
          this.isValidEventLocation = true;
          this.isValidEventMankindType = false;
          this.scrollTo('add-event__item--event-mankind-type');
          break;
        case 'next-from--event-mankind-type':
          this.isValidEventMankindType = true;
          this.isValidEventOwnerRole = false;
          this.scrollTo('add-event__item--event-owner');
          break;
        case 'next-from--event-owner':
          this.isValidEventOwnerRole = true;
          this.isValidEventOwnerLocation = false;
          this.scrollTo('add-event__item--event-owner-location');
          break;
        case 'prev-from--event-mankind-type':
          this.isValidEventMankindType = true;
          this.isValidEventLocation = false;
          this.eventMankindTypeName = '';
          this.eventMankindType = '';
          this.isReadyEventMankindType = false;
          this.isReadyEventMankindTypeName = false;
          this.scrollTo('add-event__item--event-location');
          break;
        case 'prev-from--event-owner':
          this.isValidEventOwnerRole = true;
          this.isValidEventMankindType = false;
          this.eventOwnerName = '';
          this.eventOwnerRole = '';
          this.isReadyEventOwnerName = false;
          this.isReadyEventOwnerRole = false;
          this.isReadyEventOwnerAge = false;
          this.scrollTo('add-event__item--event-mankind-type');
          break;
        case 'prev-from--event-owner-location':
          this.isValidEventOwnerLocation = true;
          this.isValidEventOwnerRole = false;
          this.eventOwnerLocationNation = '';
          this.eventOwnerLocationProvincial = '';
          this.eventOwnerLocationCity = '';
          this.isReadyEventOwnerLocationNation = false;
          this.isReadyEventOwnerLocationProvincial = false;
          this.isReadyEventOwnerLocationCity = false;
          this.scrollTo('add-event__item--event-owner');
          break;
      }
    }
  }
  counter(i: number) {
    return new Array(i);
  }
}
