import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from './home/home.component';
import {SiteNotFoundComponent} from './site-not-found/site-not-found.component';
import {NotificationsComponent} from './notifications/notifications.component';
import {AddEventComponent} from './add-event/add-event.component';
import {EventComponent} from './event/event.component';
import {EventDocumentComponent} from './event-document/event-document.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
    data: {
      title: 'Home',
    },
    children: [
      {
        path: 'home',
        // @ts-ignore
        loadChildren: () =>
          import('./home/home.module').then(
            (m) => m.HomeModule
          ),
      },
    ],
  },
  {
    path: 'home',
    component: HomeComponent,
    data: {
      title: 'Home',
    },
    children: [
      {
        path: 'home',
        // @ts-ignore
        loadChildren: () =>
          import('./home/home.module').then(
            (m) => m.HomeModule
          ),
      },
    ],
  },
  {
    path: 'notifications',
    component: NotificationsComponent,
    data: {
      title: 'Notifications',
    },
    children: [
      {
        path: 'notifications',
        // @ts-ignore
        loadChildren: () =>
          import('./notifications/notifications.module').then(
            (m) => m.NotificationsModule
          ),
      },
    ],
  },
  {
    path: 'add-event',
    component: AddEventComponent,
    data: {
      title: 'add-event',
    },
    children: [
      {
        path: 'add-event',
        // @ts-ignore
        loadChildren: () =>
          import('./add-event/add-event.module').then(
            (m) => m.AddEventModule
          ),
      },
    ],
  },
  {
    path: 'event',
    component: EventComponent,
    data: {
      title: 'event',
    },
    children: [
      {
        path: 'event',
        // @ts-ignore
        loadChildren: () =>
          import('./event/event.module').then(
            (m) => m.EventModule
          ),
      },
    ],
  },
  {
    path: 'event-document',
    component: EventDocumentComponent,
    data: {
      title: 'event-document',
    },
    children: [
      {
        path: 'event-document',
        // @ts-ignore
        loadChildren: () =>
          import('./event-document/event-document.module').then(
            (m) => m.EventDocumentModule
          ),
      },
    ],
  },

  { path: '**', component: SiteNotFoundComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
